<template>
<main>
    <CommentModal v-if="modalVisible3" @close1="modalVisible3 = false" :listdata="modalData3"/>
    <Header :header="content"/>
    <input type="text" placeholder="نام دستگاه یا بارکد مدنظر را جستجو نمایید ..." v-model="filter" style="width:500px;display:block;margin:10px auto;font-family:yekan;"/>
    <div class="container list">
        <table class="table table-hover container" >
            <thead>
                <tr>
                <th scope="col">ردیف</th>
                <th scope="col">نام دستگاه</th>
                <th scope="col">بارکد</th>
                <th scope="col">تاریخ</th>
                <th scope="col">نوع تست</th>
                <th scope="col">نتیجه تست</th>
                <th scope="col">توضیحات</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(device,index) in filteredRows" :key="device">
                <th scope="row">{{index+1}}</th>
                <td>{{device.devicename}}</td>
                <td>{{device.barcode}}</td>
                <td>{{device.date}}</td>
                <td>{{device.testtype}}</td>
                <td>{{device.result}}</td>
                <td><button class="btn btn-primary" @click="openModal3(device.comment)">مشاهده</button></td>
                </tr>
            </tbody>
        </table>
    </div>
 
</main>
</template>

<script>
import Header from '../../components/Header.vue'
import CommentModal from '../../components/CommentModal.vue'
import axios from 'axios'
export default {
    name:'TestView',
    components:{Header,CommentModal},
    data() {
        return {
            content:'نتایج تست دستگاه ها',
            filter:'',
            list:null,
            modalVisible3:false,
            modalData3:null
        }
    },
    created() {
        axios.get('../test.json')
        .then(({data})=>{
            this.list=data;
        })
    },
    computed: {
         filteredRows() {
            return this.list.filter(device => {
            const devicename = device.devicename.toString().toLowerCase();
            const barcode = device.barcode.toString();
            const searchTerm = this.filter.toLowerCase();

            return devicename.includes(searchTerm) ||
            barcode.includes(searchTerm);
            });
         }
    },
    methods: {
        openModal3(msg){
            this.modalData3 = msg;
            this.modalVisible3 = true;
        }
    },
    
      
}
</script>

<style lang="scss" scoped>
.list{
        max-height: 65vh;
        overflow-y: scroll;
        table{
            
            direction: rtl;
            background-color: rgb(255, 255, 255);
            border-radius: 2px;
            text-align: center;
    
                thead{
                    background-color: #082C68;
                    color: rgb(255, 255, 255);
                    
        
                }
                    
        }
    }
</style>